import { createAsyncThunk, createSlice, PayloadAction } from '@reduxjs/toolkit';
import { RootState } from '../store/store';
import { Product } from "../data/products/_productType";

export const fetchProducts = createAsyncThunk(
    'products/fetchProducts',
    async () => {
        const response = await fetch('../data/products.ts');
        return response;
    }
)

export interface ProductState {
    products: Product[],
    loading: boolean,
    loadingError: boolean,
    searchTerm: string,
    searchResults: {}[]
}

const initialstate: ProductState = {
    products: [],
    loading: false,
    loadingError: false,
    searchTerm: '',
    searchResults: []
}

const productSlice = createSlice({
    name: 'products',
    initialState: initialstate,
    reducers: {
        setSearchTerm: (state, action: PayloadAction<string>) => {
            state.searchTerm = action.payload;

            const searchTerm = state.searchTerm;
            const products = state.products;

            for (let i = 0; i < products.length; i++) {
                //if ((products[i].id.toString() === searchTerm) {}

                if (products[i].name.toLowerCase().includes(searchTerm) || products[i].id.toString().includes(searchTerm)) {
                    state.searchResults.push(products[i]);
                }
            }
        }
    },
    extraReducers: {}
});

export default productSlice.reducer;
export const selectProducts = (state: RootState) => state.products.products;
export const selectSearchTerm = (state: RootState) => state.products.searchTerm;

export const { setSearchTerm } = productSlice.actions;